<template>
  	<div style="height:100%;" class='main' >
		<div class="cen">
			   <img height="100%" src="../../assets/img/组 103.png"> 
			   <div class="cen-rig">
				   <span class='cen-rig-div' style='font-size:30px;text-align: center;color:#272E80FF'>智慧售楼处CRM客户管理系统</span>
				   <div class='cen-rig-div'>
					   <span class="font_size_18" style="opacity:0.5">账号</span>
					     <input 
						 style="webkit-box-shadow: 0 0 0 1000px white inset;"
						 type="text" class="inp" v-model="user.saller_tel" autocomplete="off" >
						
					   <!-- <el-input  v-model="input" ></el-input> -->
				   </div>
				   <div class='cen-rig-div'>
					   <span class="font_size_18" style="opacity:0.5">密码</span>
					     <input
						   style="webkit-box-shadow: 0 0 0 1000px white inset;"
						  type="password" class="inp" v-model="user.password"  autocomplete="off" />
				   </div>
				   <div class='cen-rig-div'>
					     <el-button @click="submitForm()" :loading="load"  >登录</el-button>
				   </div>
			   </div>
		</div>
  	</div>
</template>

<script>
	import {login} from '@/api/getData'
	import {mapActions, mapState} from 'vuex'
	import {setStore} from '@/config/mUtils'
	export default {
	    data(){
			return {
				// 加载
			  	 load:false,
				 user:{
					 saller_tel:'',
					 password:''
				 },
				
				rules: {
					username: [
			            { required: true, message: '请输入用户名', trigger: 'blur' },
			        ],
					password: [
						{ required: true, message: '请输入密码', trigger: 'blur' }
					],
				},
				showLogin: false,
			}
		},
		mounted(){
			this.showLogin = true;
			// if (!this.adminInfo.id) {
    		// 	// this.getAdminData()
    		// }
		},
		computed: {
	
		},
		methods: {
		
			async submitForm() {
					if (!this.user.saller_tel) {
						this.$message({
							type: 'error',
							message: '电话号码不可为空'
						});
						return;
					}
					if (!this.user.password) {
						this.$message({
							type: 'error',
							message: '密码不可为空'
						});
						return;
					}
					// console.log(this.user);
					this.load=true;
					const res = await login(this.user);
					this.load=false;
					if (res.code!=0) {
						this.$message({
							type: 'error',
							message: res.msg
						});
						return;
					}
					// 保存数据
					setStore('user',res.data);
				  
					// 跳转页面
					if (res.data.auth==100) {
						this.$router.push('team')
					}
					else{
						this.$router.push('menu-com')
					}
				
		
			 },
			
		},
		watch: {
			// adminInfo: function (newValue){
			// 	if (newValue.id) {
			// 		this.$message({
            //             type: 'success',
            //             message: '检测到您之前登录过，将自动登录'
            //         });
			// 		this.$router.push('manage')
			// 	}
			// }
		}
	}
</script>

<style lang="less" scoped>
	@import '../../style/mixin';
	.el-input{
		border: none;
	}
	.main{
		display: flex;
		align-items: center;
		width: 100%;
		height: 100%;
		background-image: url("../../assets/img/back.png");
		// background-image: url('');

	}
	.cen{
		display: flex;
		align-items: center;
		width:79%;
		height:83%;
		// background-color: red;
		margin: 0 auto;
	}
	.cen-rig{
		width:48%;
		height:85%;
		display: flex;
		 flex-direction: column; 
		 justify-content:space-around;
		 align-items: center;
		background-color: #FFF;
		// border: ;
	}
	.cen-rig-div{
		width:54%;
		// height: 30px;
		// background-color: blue;
	}
	.cen-rig{
    	box-shadow:7px 4px 7px rgba(0, 0, 0, 0.1) ;
	}
	
	.inp{
		display: block;
		border: none;
		border-bottom: 1px solid rgba(0, 0, 0, 0.2) ;
		margin-top:10px ;
		width:100%;
		height:30px;
		// background:#fff !important;
	}
	.el-button{
		width:100%;
		background-color:rgb(38,49,128);
		color:white;
	}
//  .inp :focus { 
//         outline: none;
//         background-color: transparent;
//     }
//     ::selection{background:transparent; }
//     ::-moz-selection{background:transparent; }
</style>



